@import url(https://fonts.googleapis.com/css?family=Hammersmith+One&display=swap);
body {
  padding: 10px;
  margin: 0; }

.main-title {
  font-size: 36px;
  line-height: 40px;
  font-family: "Hammersmith One", sans-serif;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.4);
  color: #6cf; }

.carousel {
  position: relative;
  padding: 0 40px;
  border: 1px solid #ccc;
  border-radius: 15px;
  background: #eee; }
  .carousel__arrow {
    position: absolute;
    padding: 0;
    background: #ddd;
    border: 1px solid gray;
    font-size: 20px;
    color: #444;
    display: block; }
    .carousel__arrow:focus {
      outline: none; }
    .carousel__arrow:hover {
      background: #ccc;
      cursor: pointer; }
    .carousel__arrow--prev {
      left: 7px; }
    .carousel__arrow--next {
      right: 7px; }
  .carousel__container {
    overflow: hidden; }
  .carousel__list {
    width: 9999px;
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 0; }
  .carousel__item {
    display: inline-block; }
  .carousel__img {
    display: block; }

